/* Styling for Cards */
.Card {
    backdrop-filter: blur(5px);
    border: 2px solid #ccc;
    border-radius: 25px;
    padding: 30px;
    margin-bottom: 40px;
    width: 400px;
    text-align: center;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
    background-color: rgba(255, 255, 255, 0.9); /* Slightly opaque white background */
}

/* Title Styling */
.title {
    color: #0b1992;
    font-size: 32px; /* Increased font size for better emphasis */
    font-weight: bold;
    margin-bottom: 20px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

/* Author Styling */
.author {
    color: #666;
    font-size: 18px; /* Slightly smaller font size */
    margin-bottom: 15px; /* Adjusted margin for better spacing */
}

/* Description Styling */
.description {
    color: #ffffffd0;
    text-align: justify;
    padding: 0 20px;
    margin-bottom: 20px;
    font-size: 16px;
}

/* Bet Button Styling */
.betButton {
    border-radius: 25px;
    border: none;
    padding: 12px 30px;
    font-size: 18px;
    font-weight: bold;
    font-family: Arial, sans-serif;
    background-color: #0ca1e7;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
}

.betButton:hover {
    background-color: #540788;
}

/* More Button Styling */
.moreButton {
    width: 30px;
    float: right;
    color: #666;
    font-size: 24px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.moreButton:hover {
    color: #333;
}

/* Image Styling */
.Card img {
    max-width: 100%; /* Ensure image does not exceed card width */
    height: auto; /* Maintain aspect ratio */
    border-radius: 15px; /* Rounded corners */
    margin-bottom: 20px; /* Adjusted margin for spacing */
}

/* Timestamp Styling */
.timestamp {
    color: #666;
    font-size: 12px; /* Adjust the font size as needed */
    margin-top: 10px; /* Adjust the margin for spacing */
}

